import { Transliterator, User } from './types';

export const getUserPreferedLanguages = (user: User): string[] => {
  const { primary, secondary } = user.preference.language;
  return [primary, ...secondary];
};

export const findTransliteratorsOfLanguage = (
  language: string[],
  tMap: Record<number, Transliterator>
): Record<number, Transliterator> => {
  if (!language.length) {
    return tMap;
  }
  return Object.entries(tMap)
    .filter(([id, transliterator]) =>
      language.some((lang) => lang === transliterator.lang)
    )
    .reduce((state, [key, value]) => ({ ...state, [key]: value }), {});
};

export type UserTransliteratorMap = [Transliterator[], Transliterator[]];

type getUserTransliteratorsType = (
  u: User,
  tMap: Record<number, Transliterator>
) => UserTransliteratorMap;
export const getUserTransliterators: getUserTransliteratorsType = (
  user,
  transliteratorsMap
) => {
  const result: UserTransliteratorMap = [[], []];
  const userLangs = getUserPreferedLanguages(user);
  const userTMap = findTransliteratorsOfLanguage(userLangs, transliteratorsMap);
  const userLikedTransliterators = user.preference?.transilterators || [];
  if (userLikedTransliterators.length) {
    const allowedTransliteratorIds = Object.keys(userTMap).map((t) =>
      parseInt(t)
    );
    result[0] = userLikedTransliterators
      .filter((t) => allowedTransliteratorIds.includes(t))
      .map((t) => userTMap[t]);
    result[1] = allowedTransliteratorIds
      .filter((t) => !userLikedTransliterators.includes(t))
      .map((t) => userTMap[t]);
  } else {
    result[0] = Object.values(userTMap);
  }
  return result;
};
