import React from 'react';

import { makeStyles } from '@material-ui/core';
import { renderRoutes, RouteConfigComponentProps } from 'react-router-config';

const useStyles = makeStyles((theme) => ({
  root: {},
}));

type ListWithRouteViewerProps = RouteConfigComponentProps;

export const ListCategoryViewer: React.FC<ListWithRouteViewerProps> = ({
  route,
}: ListWithRouteViewerProps) => {
  const classes = useStyles();
  return <div className={classes.root}>{renderRoutes(route?.routes)}</div>;
};
