import * as React from 'react';

import { useSelector } from 'react-redux';
import { useFirestore } from 'react-redux-firebase';
import { AppState } from 'src/state';

import { getUserTransliterators, Kural } from '../api';
import { getKural, getKuralDetail, KuralDetail, Transliterator } from '../api';

type useKuralDetailType = (
  kural: Kural | number
) => [Kural | null, KuralDetail | null, Transliterator[]];

export const useKuralDetail: useKuralDetailType = (kuralInput) => {
  const firestore = useFirestore();
  const user = useSelector((st: AppState) => st.user);
  const { primary: PRIMARY_LANG } = user.preference.language;
  const TRANSLITERATORS = useSelector(
    (st: AppState) => st.kuralIndexMap.transliterator
  );
  const [kural, setKural] = React.useState<Kural | null>(null);
  const [kuralDetail, setKuralDetail] = React.useState<KuralDetail | null>(
    null
  );
  const [userPreferedTranslits] = getUserTransliterators(user, TRANSLITERATORS);

  React.useEffect(() => {
    (async (): Promise<void> => {
      // TODO: get kuralDetail only of user language
      if (typeof kuralInput === 'number') {
        setKural(await getKural(firestore, kuralInput, PRIMARY_LANG));
        setKuralDetail(await getKuralDetail(firestore, kuralInput));
      } else {
        setKural(kuralInput);
        setKuralDetail(await getKuralDetail(firestore, kuralInput.id));
      }
    })();
  }, [firestore, kuralInput, PRIMARY_LANG]);
  // const filteredTransliteratorMap = Object.values(TRANSLITERATORS)
  //   .filter((trans) => trans.lang === PRIMARY_LANG)
  //   .sort((t1, t2) => {
  //     const [t1r, t2r] = [t1.rank, t2.rank];
  //     return t1r && t2r ? t1r - t2r : t1r ? -1 : 1;
  //   });
  return [kural, kuralDetail, userPreferedTranslits];
};
