import React from 'react';

import {
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  ListSubheader,
  makeStyles,
} from '@material-ui/core';
import { DragIndicator } from '@material-ui/icons';
import clsx from 'clsx';
import {
  DragDropContext,
  Droppable,
  Draggable,
  DropResult,
  ResponderProvided,
} from 'react-beautiful-dnd';
import { useDispatch, useSelector } from 'react-redux';
import { useFirestore } from 'react-redux-firebase';
import {
  getUserTransliterators,
  Transliterator,
  User,
  updateUserPreference,
} from 'src/lib/api';
import { AppState } from 'src/state';
import { applyPreference } from 'src/state/settings';

const useStyles = makeStyles((theme) => ({
  root: {},
  subList: {
    '&.dragging-over': {
      backgroundColor: 'lightblue',
    },
  },
  subHeader: {
    textAlign: 'left',
    lineHeight: '1.5em',
  },
  subListItem: {
    '&.is-dragging': {
      backgroundColor: 'red',
    },
  },
  subListItemText: {
    fontSize: '0.8em',
  },
}));

type RankTransliteratorSettingProps = {
  user: User;
  className?: string;
};

enum TransliteratorSectionType {
  ACTIVE = 'active',
  INACTIVE = 'inactive',
}
type TransliteratorSectionMapType = Record<
  TransliteratorSectionType,
  Transliterator[]
>;

const TransliteratorLabel: Record<TransliteratorSectionType, string> = {
  active: 'Active',
  inactive: 'InActive',
};

const toUserLikedTranslitsMap = (
  user: User,
  transliterators: Record<number, Transliterator>
): TransliteratorSectionMapType => {
  const [activeList, inActiveList] = getUserTransliterators(
    user,
    transliterators
  );
  return { active: activeList, inactive: inActiveList };
};

export const RankTransliteratorSetting: React.FC<RankTransliteratorSettingProps> = (
  props: RankTransliteratorSettingProps
) => {
  const classes = useStyles();
  const firestore = useFirestore();
  const dispatch = useDispatch();
  const transliterators = useSelector(
    (st: AppState) => st.kuralIndexMap.transliterator
  );
  const [userTransLits, setUserTransLits] = React.useState(
    toUserLikedTranslitsMap(props.user, transliterators)
  );
  React.useEffect(() => {
    setUserTransLits(toUserLikedTranslitsMap(props.user, transliterators));
  }, [props.user, transliterators]);

  const onDragEnd = (
    { source, destination }: DropResult,
    _: ResponderProvided
  ): void => {
    let updateState = false;
    if (!destination) {
      return;
    }
    const reorderedTransliterors = JSON.parse(
      JSON.stringify(userTransLits)
    ) as TransliteratorSectionMapType;
    const srcList =
      reorderedTransliterors[source.droppableId as TransliteratorSectionType];
    if (source.droppableId !== destination.droppableId) {
      const destList =
        reorderedTransliterors[
          destination.droppableId as TransliteratorSectionType
        ];
      destList.splice(destination.index, 0, srcList[source.index]);
      srcList.splice(source.index, 1);
      updateState = true;
    } else if (source.index !== destination.index) {
      srcList.splice(destination.index, 0, ...srcList.splice(source.index, 1));
      updateState = true;
    }
    if (updateState) {
      const userPreference = {
        ...props.user.preference,
        transilterators: reorderedTransliterors.active.map((t) => t.id),
      };
      dispatch(applyPreference(userPreference));
      updateUserPreference(firestore, {
        ...props.user,
        preference: userPreference,
      });
    }
  };
  return (
    <DragDropContext onDragEnd={onDragEnd}>
      {Object.entries(userTransLits).map(([key, list]) => (
        <Droppable droppableId={key} key={key}>
          {(provided, snapshot) => (
            <List
              {...provided.droppableProps}
              ref={provided.innerRef}
              className={clsx(
                classes.subList,
                snapshot.isDraggingOver && 'dragging-over'
              )}
              subheader={
                <ListSubheader component='div' className={classes.subHeader}>
                  {TransliteratorLabel[key as TransliteratorSectionType]}
                </ListSubheader>
              }>
              {list.map((tItem, index) => (
                <Draggable
                  key={tItem.id}
                  draggableId={tItem.id.toString()}
                  index={index}>
                  {(provided, snapshot) => (
                    <ListItem
                      className={clsx(
                        classes.subListItem,
                        snapshot.isDragging && 'is-dragging'
                      )}
                      style={provided.draggableProps.style}
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      {...provided.dragHandleProps}>
                      <ListItemIcon>
                        <DragIndicator />
                      </ListItemIcon>
                      <ListItemText
                        disableTypography
                        primary={tItem.name}
                        className={classes.subListItemText}
                      />
                    </ListItem>
                  )}
                </Draggable>
              ))}
              {provided.placeholder}
            </List>
          )}
        </Droppable>
      ))}
    </DragDropContext>
  );
};
