import React, { FC, PropsWithChildren } from 'react';

import {
  Collapse,
  ListItem,
  ListItemIcon,
  ListItemText,
  makeStyles,
} from '@material-ui/core';
import clsx from 'clsx';

import { AnimatedExpandedIcon } from './../../components/common/AnimatedExpandedIcon';

const useStyles = makeStyles((theme) => ({
  root: {},
  itemLabel: {
    fontSize: '1em',
  },
  itemChild: {
    padding: theme.spacing(1),
  },
  listItemIcon: {},
}));

type RankTransliteratorListProps = PropsWithChildren<{
  label: string;
  className?: string;
  isOpen?: boolean;
  showExpandIcon?: boolean;
}>;

export const SettingItem: FC<RankTransliteratorListProps> = ({
  label,
  className,
  isOpen = false,
  showExpandIcon = true,
  children,
}: RankTransliteratorListProps) => {
  const classes = useStyles();
  const [isItemExpanded, toggleItem] = React.useState(isOpen);
  return (
    <>
      <ListItem
        button
        className={clsx(className, classes.root)}
        onClick={(): void => toggleItem(!isItemExpanded)}>
        <ListItemText
          classes={{ primary: classes.itemLabel }}
          primary={label}
        />
        {showExpandIcon && (
          <ListItemIcon className={classes.listItemIcon}>
            <AnimatedExpandedIcon isExpanded={isItemExpanded} />
          </ListItemIcon>
        )}
      </ListItem>
      <Collapse
        classes={{ container: classes.itemChild }}
        in={isItemExpanded}
        timeout='auto'
        unmountOnExit>
        {children}
      </Collapse>
    </>
  );
};
