export type IndexMapType = {
  section: {
    [key: number]: {
      litIndex: [number, number];
      chapIndex: [number, number];
    };
  };
  litChapMap: { [key: number]: [number, number] };
};

export type Transliterator = {
  id: number;
  name: string;
  lang: string;
  rank?: number;
};

type BaseType = {
  value: string;
  id: number;
};

type Kural = BaseType;
type Section = BaseType;
type Literature = BaseType;
type Chapter = BaseType;

type Category = 'section' | 'chapter' | 'literature' | 'kural';
type RootCategory = Exclude<Category, 'kural'>;
type CategoryBaseTypeMap = Record<Category, BaseType>;

type KuralMixin = {
  kural: Kural;
  section: Section;
  literature: Literature;
  chapter: Chapter;
};

type SearchResult = {
  type: SearchTypes;
  item: BaseType;
  category: Category;
  keyword: string;
};

type SectionSearchType = 'BySectionNo' | 'BySectionContains';
type LiteratureSearchType = 'ByLiteratureNo' | 'ByLiteratureContains';
type ChapterSearchType = 'ByChapterNo' | 'ByChapterContains';
type KuralSearchType =
  | 'ByKuralNo'
  | 'ByKuralContains'
  | 'ByKuralStartsWith'
  | 'ByKuralEndsWith';
type KuralDetailSearchType = 'ByKuralDetailContains';
type SearchTypes =
  | KuralSearchType
  | SectionSearchType
  | LiteratureSearchType
  | ChapterSearchType
  | KuralDetailSearchType;

export type KuralDetail = Record<string, string>;
export type {
  BaseType,
  Kural,
  Section,
  Literature,
  Chapter,
  KuralMixin,
  Category,
  RootCategory,
  CategoryBaseTypeMap,
  SearchResult,
  SearchTypes,
};

export type SearchCategoryTypes = Category | 'detail' | 'all';

export enum FontSizeType {
  TINY = 'TINY',
  SMALL = 'SMALL',
  MEDIUM = 'MEDIUM',
  LARGE = 'LARGE',
  BIG = 'BIG',
}

export type UserPreference = {
  language: {
    primary: string;
    secondary: string[];
  };
  fontSizeType: FontSizeType;
  transilterators?: number[];
};
export type User = {
  id: string;
  favorites: Record<Exclude<Category, 'section' | 'literature'>, number[]>;
  preference: UserPreference;
};
