import React from 'react';

import { RouteConfig, RouteConfigComponentProps } from 'react-router-config';
import { Redirect } from 'react-router-dom';

import { isCategoryValid } from './lib/api';
import { CategoryViewerContainer } from './views/components/category-viewer';
import KuralDetailViewer from './views/components/kural/KuralDetailViewer';
import { KuralListSwipeable } from './views/components/kural/KuralsByChapterViewer';
import Home from './views/pages/home';
import { ListCategoryViewer } from './views/pages/list';
import Main from './views/pages/main/main';
import Search from './views/pages/search/search';
import SettingsViewer from './views/pages/settings';

export const ROUTE_CONSTANTS = {
  ROOT: '/',
  LIST: '/list',
  LIST_CATEGORY: '/list',
  SEARCH: '/search',
  FAVOURITE: '/favorite',
  SETTINGS: '/settings',
};

export const routes: RouteConfig[] = [
  {
    path: ROUTE_CONSTANTS.ROOT,
    component: Main,
    routes: [
      {
        path: ROUTE_CONSTANTS.SEARCH,
        component: Search,
        exact: true,
      },
      {
        path: ROUTE_CONSTANTS.SETTINGS,
        component: SettingsViewer,
        exact: true,
      },
      {
        path: ROUTE_CONSTANTS.LIST,
        component: ListCategoryViewer,
        routes: [
          {
            path: `${ROUTE_CONSTANTS.LIST}/:section/:literature/:chapter`,
            component: KuralListSwipeable,
            exact: true,
          },
          {
            path: `${ROUTE_CONSTANTS.LIST}/:section/:literature/:chapter/:kural`,
            component: KuralDetailViewer,
            exact: true,
          },
          {
            path: `${ROUTE_CONSTANTS.LIST_CATEGORY}*`,
            // eslint-disable-next-line react/display-name
            render: ({ match }: RouteConfigComponentProps): React.ReactNode => {
              const route = match.url.replace(ROUTE_CONSTANTS.LIST, '');
              const ROUTE_REGEX = /^(\/\d+)?(\/\d+)?$/;
              if (ROUTE_REGEX.test(route)) {
                const [section, literature] = [
                  route.replace(ROUTE_REGEX, '$1').replace('/', ''),
                  route.replace(ROUTE_REGEX, '$2').replace('/', ''),
                ];
                if (
                  (section === '' && literature === '') ||
                  (section.length > 0 &&
                    isCategoryValid('section', parseInt(section))) ||
                  (literature.length > 0 &&
                    isCategoryValid('literature', parseInt(literature)))
                ) {
                  return <CategoryViewerContainer />;
                }
              }
              return <Redirect to={`${ROUTE_CONSTANTS.LIST}/1/1/1`} />;
            },
          },
        ],
      },

      {
        path: ROUTE_CONSTANTS.ROOT,
        exact: true,
        component: Home,
      },
      {
        path: '/*',
        // eslint-disable-next-line react/display-name
        component: (): React.ReactElement => (
          <Redirect to={ROUTE_CONSTANTS.ROOT} />
        ),
      },
    ],
  },
];
