import * as React from 'react';

import { ListItem, ListItemText, makeStyles } from '@material-ui/core';
import { TreeMenuItem } from 'react-simple-tree-menu';

import { AnimatedExpandedIcon } from '../common/AnimatedExpandedIcon';

const useStyles = makeStyles((theme) => ({
  listItem: {
    cursor: 'pointer',
    position: 'relative',
    borderRadius: theme.spacing(2),
    WebkitTapHighlightColor: 'transparent',
    border: 'none',
    '&.level-0': {
      paddingLeft: theme.spacing(2),
    },
    '&.level-1': {
      paddingLeft: theme.spacing(3),
    },
    '&.level-2': {
      paddingLeft: theme.spacing(4),
    },
    '&.on-focus': {
      color: theme.palette.secondary.main,
    },
    '&.is-active': {
      color: theme.palette.primary.main,
      border: '2px solid',
    },
  },
  listIcon: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
}));

export const TreeListItem: React.FC<TreeMenuItem> = ({
  level = 0,
  hasNodes,
  isOpen,
  label,
  toggleNode,
  focused,
  onClick,
  active,
  ...props
}: TreeMenuItem) => {
  const classes = useStyles();

  const toggleCategory = (ev: React.MouseEvent<HTMLLIElement, MouseEvent>) => {
    hasNodes && toggleNode && toggleNode();
    onClick(ev);
  };
  return (
    <ListItem
      onClick={toggleCategory}
      className={`${classes.listItem} level-${level} ${focused && 'on-focus'} ${
        active && 'is-active'
      }`}>
      <ListItemText primary={label}></ListItemText>
      {hasNodes && (
        <div className={classes.listIcon}>
          <AnimatedExpandedIcon isExpanded={isOpen} />
        </div>
      )}
    </ListItem>
  );
};
