import * as React from 'react';

import { Slider, SliderTypeMap, withStyles } from '@material-ui/core';
import { DefaultComponentProps } from '@material-ui/core/OverridableComponent';

export type DiscreteSliderProps = Partial<DefaultComponentProps<SliderTypeMap>>;

const StyledSlider = withStyles({
  root: {
    height: 8,
  },
  thumb: {
    height: 20,
    width: 20,
    marginTop: '-7px',
    backgroundColor: 'currentColor',
    '&:focus, &:hover, &$active': {
      boxShadow:
        'rgba(136, 153, 166, 0.2) 0px 0px 7px, rgba(136, 153, 166, 0.15) 0px 1px 3px 1px',
    },
  },
  active: {},
  track: {
    height: 8,
    borderRadius: 4,
  },
  rail: {
    height: 8,
    borderRadius: 4,
  },
  mark: {
    width: 16,
    height: 16,
    borderRadius: '50%',
    marginLeft: '-6px',
    opacity: 0.8,
    marginTop: '-5px',
    '& ::after': {
      top: '-15px',
      left: '-15px',
      right: '-15px',
      bottom: '-15px',
      content: '"',
      position: 'absolute',
      borderRadius: '50%',
      border: '1px solid',
    },
  },
  markActive: {
    opacity: 1,
    backgroundColor: 'currentColor',
  },
})(Slider);

export const DiscreteSlider: React.FC<DiscreteSliderProps> = (props) => {
  return (
    <StyledSlider
      aria-labelledby='discrete-slider'
      valueLabelDisplay='auto'
      {...props}
    />
  );
};

export default DiscreteSlider;
