/* eslint-disable react/prop-types */
import React from 'react';

import { List, makeStyles } from '@material-ui/core';
import { debug } from 'loglevel';
import TreeMenu, { TreeMenuProps } from 'react-simple-tree-menu';

import { TreeListItem } from './category-list-item';

const useStyles = makeStyles((theme) => ({
  treeMenuContainer: {
    '&>div': {
      outlineStyle: 'none',
    },
  },
  list: {
    padding: `0px ${theme.spacing(1)}px`,
  },
  listItem: {
    cursor: 'pointer',
    position: 'relative',
    borderRadius: theme.spacing(2),
    WebkitTapHighlightColor: 'transparent',
    border: 'none',
    '&.level-0': {
      paddingLeft: theme.spacing(2),
    },
    '&.level-1': {
      paddingLeft: theme.spacing(3),
    },
    '&.level-2': {
      paddingLeft: theme.spacing(4),
    },
    '&.on-focus': {
      color: theme.palette.secondary.main,
    },
    '&.is-active': {
      color: theme.palette.primary.main,
      border: '2px solid',
    },
  },
  listIcon: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    transition: 'all 0.2s ease',
    transform: 'rotate(0deg)',
    '&.is-open': {
      transform: 'rotate(-180deg)',
    },
  },
}));

type CategoryViewerProps = {
  className?: string;
} & TreeMenuProps;

export const CategoryViewer: React.FC<CategoryViewerProps> = ({
  className,
  ...props
}: CategoryViewerProps) => {
  const classes = useStyles();
  debug('CategoryViewer renders');
  return (
    <div className={classes.treeMenuContainer}>
      <TreeMenu {...props}>
        {({ items }) => (
          <List className={classes.list}>
            {items.map(({ key, ...itemProps }) => (
              <TreeListItem key={key} {...itemProps} />
            ))}
          </List>
        )}
      </TreeMenu>
    </div>
  );
};
