import * as React from 'react';

import { makeStyles } from '@material-ui/core';
import clsx from 'clsx';
import { useDispatch } from 'react-redux';
import { useFirestore } from 'react-redux-firebase';
import {
  FONT_SIZE_MAP,
  User,
  FontSizeType,
  updateUserPreference,
} from 'src/lib/api';
import { applyPreference, ANONYOMOUS_USER } from 'src/state/settings';

import { CommonComponentProps } from '../../components';
import { DiscreteSlider } from './../../components/common/DiscreteSlider';

const useStyles = makeStyles((theme) => ({
  sliderWrapper: {
    display: 'flex',
    marginLeft: theme.spacing(3),
    alignItems: 'center',
  },
  sliderRoot: {
    width: '80%',
  },
}));

export interface FontSizePickerProps extends Partial<CommonComponentProps> {
  user: User;
}

export const FontSizePicker: React.FC<FontSizePickerProps> = (
  props: FontSizePickerProps
) => {
  const classes = useStyles();
  const firestore = useFirestore();
  const dispatch = useDispatch();
  const fontSize =
    FONT_SIZE_MAP[
      props.user.preference.fontSizeType ||
        ANONYOMOUS_USER.preference.fontSizeType
    ];
  const marks = Object.entries(FONT_SIZE_MAP).map(([, v]) => ({
    value: v,
  }));
  const onFontChange = (
    _: React.ChangeEvent<unknown>,
    value: number | number[]
  ) => {
    if (typeof value === 'number') {
      const fontType = Object.keys(FONT_SIZE_MAP).find(
        (type) => FONT_SIZE_MAP[type as FontSizeType] === value
      );
      const userPreference = {
        ...props.user.preference,
        fontSizeType: fontType!! as FontSizeType,
      };
      dispatch(applyPreference(userPreference));
      updateUserPreference(firestore, {
        ...props.user,
        preference: userPreference,
      });
    }
  };
  return (
    <div className={clsx(`${props.className}-wrapper`, classes.sliderWrapper)}>
      <DiscreteSlider
        classes={{ root: classes.sliderRoot }}
        {...props}
        aria-label='font-size-picker'
        value={fontSize}
        valueLabelDisplay={'off'}
        step={null}
        marks={marks}
        min={FONT_SIZE_MAP.TINY}
        max={FONT_SIZE_MAP.BIG}
        onChange={onFontChange}
      />
    </div>
  );
};
